body {
  background-color: #000;
  color: #00fff2;
  font-family: 'Orbitron', sans-serif;
  text-align: center;
}

.neon-header {
  font-size: 2.5rem;
  text-shadow: 0 0 10px #00fff2, 0 0 20px #00fff2, 0 0 40px #00fff2;
  margin-top: 20px;
}

.neon-header p {
  font-size: 1.1rem;
  text-shadow: 0 0 10px #00fff2;
}

.game-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
  padding: 40px;
  justify-items: center;
}

.form-section, .review-section {
  margin: 60px 0;
  padding: 20px;
  background-color: rgba(0, 0, 0, 0.6);
  border: 2px solid #00fff2;
  border-radius: 15px;
  box-shadow: 0 0 20px #00fff2;
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}

button {
  background: black;
  color: #00fff2;
  border: 2px solid #00fff2;
  padding: 10px 20px;
  border-radius: 8px;
  text-shadow: 0 0 5px #00fff2;
  transition: all 0.3s;
}

button:hover {
  background: #00fff2;
  color: black;
  box-shadow: 0 0 20px #00fff2;
}